@use "sass:math";

////////////////////
// インナー幅設定
////////////////////
// コンテンツのインナー幅（最も共通の幅が多い部分）
$l-inner: 1970px;
// ※キュンパス幅ではなくインナーの幅を指す

// 余白
$padding-pc: 0;
$padding-sp: 0;
// ※PC時：デザインカンプの余白の幅ではない。PC時は通常25pxで大丈夫！


// remへの計算式（16pxを基準としている。10pxを基準とする場合は16を10に変更する）
@function rem($pixels) {
  @return math.div($pixels, 16) * 1rem;
}

// ナビゲーションを開いた時に背景がスクロールしないように
// html.is-fixed,
// html.is-fixed body {
//   @media all and (max-width: 767px) {
//     height: 100%;
//     overflow: hidden;
//   }
// }

// color
$colorWhite: #ffffff !default;
$colorbase: #535353 !default;
$colorblack: #000000 !default;
$colorSkyBlue: #00CED1 !default;
$colorGreen: #CCFF00 !default;
$colorNumGray: #474646 !default;


$base-font-family: "Hiragino Sans", "Hiragino Kaku Gothic ProN", sans-serif;
$second-font-family: 'Futura', 'Century Gothic' , sans-serif;
$third-font-family: "Hiragino Kaku Gothic ProN", "Hiragino Sans",  sans-serif;


// フォントウェイト
$regular: 300;
$normal: 500;
$bold: 700;

// パス
$imgPath:'../images/';
$fontsPath:'../fonts/';

// ブレイクポイント
$breakpoints:
('xs': ( min-width: 320px,
  ),
  's': (
    min-width: 375px,
  ),
  'sm': (
    min-width: 520px,
  ),
  'md': (
    min-width: 768px,
  ),
  'lg': (
    min-width: 960px,
  ),
  'tab': (
    min-width: 1024px,
  ),
  'xl': (
    min-width: 1366px,
  ),
  'pc-md': (
    min-width: 1536px,
  ),
  'pc-lg': (
    min-width: 1920px,
  ),
) !default;