@use 'global'as *;

.p-nav-sp {
  position: fixed;
  top: 84px;
  left: -100%;
  width: 100%;
  z-index: 5;
  transition: all .5s;
  @include mq(lg) {
    display: none;
  }
}

.p-nav-sp.open {
  left: 0;
}

.p-nav-sp__list {
  padding: 20px 20px 17px;
  font-size: rem(20);
  font-weight: $normal;
  font-family: $second-font-family;
  text-transform: uppercase;
  background: $colorSkyBlue;
  background: rgba(0, 206, 209, 0.9);
  color: $colorWhite;
}

.p-nav-sp__list:not(:first-child) {
  margin-top: 1px;
}

.p-nav-sp__list a {
  display: flex;
  align-items: center;
}

.p-nav-sp__list span {
  margin-left: 11px;
  line-height: 1;
  font-size: rem(13);
  font-weight: $regular;
  font-family: $base-font-family;
}

.p-nav-sp__list--contact {
  padding: 18px 20px 17px;
  font-weight: $bold;
  color: $colorGreen;
  background: rgba(83, 83, 83, 0.8);
  letter-spacing: 0.05em;
}

.p-nav-sp__list--contact a::after {
  content: "";
  background: url(../images/icon-mail-green.svg) no-repeat center;
  background-size: contain;
  max-width: rem(25);
  width: 100%;
  height: rem(25);
  margin-left: 9.4px;
}