@use 'global'as *;

.p-contact__inner {
  margin-top: 68px;
  @include mq(md) {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-column-gap: 7%;
    max-width: rem(1500);
    width: 85%;
    margin: rem(100) auto 0;
    padding: 0 20px;
  }
  @include mq(pc-md) {
    max-width: rem(1540);
    width: 85%;
    grid-column-gap: 6%;
  }
}

.p-contact__tel {
  display: block;
  text-align: center;
  max-width: rem(300);
  width: 81.35%;
  margin: 0 auto;
  background: #ffffff;
  border-radius: rem(20);
  padding: 62.1px 20px;
  box-shadow: 0px 3px 6px rgba(0, 0, 0, 0.16);
  @include mq(md) {
    max-width: rem(667);
    width: 100%;
    padding: 6% 8%;
    border-radius: rem(30);
  }
  @include mq(tab) {
    padding: 10% 8%;
  }
  @include mq(pc-md) {
    max-width: rem(667);
    width: 100%;
    padding-top: 6.7%;
    padding-bottom: 4.47%;
  }
  @include mq(pc-lg) {
    max-width: rem(687);
  }
}

.p-contact__tel:hover {
  opacity: 1;
}

.p-contact__tel:hover ul {
  opacity: 0.7;
}

.p-contact__mail {
  max-width: rem(300);
  width: 81.35%;
  margin: 37px auto 0;
  background: #ffffff;
  border-radius: rem(20);
  padding: 60.1px 20px;
  text-align: center;
  box-shadow: 0px 3px 6px rgba(0, 0, 0, 0.16);
  @include mq(md) {
    max-width: rem(667);
    width: 100%;
    margin-top: 0;
    padding: 6% 8%;
  }
  @include mq(tab) {
    padding: 10% 8%;
  }
  @include mq(pc-md) {
    max-width: rem(667);
    width: 100%;
    margin-top: 0;
    padding-top: 45px;
  }
  @include mq(pc-lg) {
    max-width: rem(687);
    padding: 7% 8% 4%;
  }
}

.p-contact__title {
  margin-bottom: rem(6);
  font-size: rem(20);
  text-transform: uppercase;
  font-weight: $normal;
  font-family: $second-font-family;
  @include mq(md) {
    margin-bottom: rem(80);
    font-size: rem(50);
  }
  @include mq(pc-md) {
    margin-bottom: 80px;
  }
}

.p-contact__title--mail {
  @include mq(md) {
    margin-bottom: 15.64%;
  }
  @include mq(pc-lg) {
    margin-bottom: 100px;
  }
}

.p-contact__tel-icon {
  max-width: rem(50);
  width: 100%;
  margin: 0 auto rem(5);
  height: rem(60);
  @include mq(md) {
    max-width: rem(193.75);
    height: rem(193.75);
    
  }
  @include mq(pc-md) {
    max-width: rem(193.75);
    height: rem(193.75);
    margin-bottom: rem(54);
  }
}

.p-contact__tel-number {
  display: block;
  margin-bottom: 10px;
  font-size: rem(30);
  font-weight: $normal;
  line-height: 1.2;
  font-family: $second-font-family;
  @include mq(md) {
    margin-top: rem(50);
    margin-bottom: rem(30);
    font-size: rem(60);
  }
  @include mq(pc-md) {
    margin-bottom: rem(50);
    font-size: rem(60);
  }
  @include mq(pc-lg) {
    margin-bottom: 58px;
  }
}

.p-contact__text {
  font-size: rem(13);
  font-weight: $regular;
  line-height: 1.3846;
  @include mq(md) {
    font-size: rem(30);
    line-height: 1.5;
    // font-size: clamp(16px, 2vw, 30px);
  }
}

.p-contact__mail-icon {
  max-width: rem(60);
  width: 100%;
  margin: 11px auto 0;
  height: rem(40);
  @include mq(md) {
    max-width: rem(222.5);
    height: rem(154);
  }
}

.p-contact__mail-link {
  display: block;
  max-width: rem(245);
  width: 100%;
  margin: 16px auto 15px;
  padding: 8px 0;
  border-radius: rem(30);
  font-size: rem(20);
  font-weight: $normal;
  color: $colorWhite;
  background: $colorSkyBlue;
  box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.16);
  @include mq(md) {
    max-width: rem(538);
    margin-top: rem(75);
    margin-bottom: rem(27);
    padding: rem(25) 0;
    font-size: rem(45);
    border-radius: rem(48);
    line-height: 1;
  }
  @include mq(pc-md) {
    max-width: rem(538);
    margin-top: rem(75);
    margin-bottom: rem(27);
    padding: 25px 0;
    font-size: rem(45);
    border-radius: rem(48);
    line-height: 1;
  }
  @include mq(pc-lg) {
    margin-bottom: 34px;
  }
}

.p-contact__bg-line {
  position: relative;
}

.p-contact__bg-line::before {
  content: "";
  position: absolute;
  background: $colorSkyBlue;
  top: 50%;
  left: 0;
  width: 100vw;
  height: rem(284);
  margin: 0 calc(50% - 50vw);
  transform: translateY(-50%) skewY(339.5deg);
  transform-origin: top center;
  z-index: -1;
  @include mq(md) {
    height: rem(750);
    left: 0;
    transform: translateY(-50%) skewY(349deg);
    // height: rem(750);
    // top: 0;
    // left: 0;
    // transform: translateY(-28%) skewY(349deg);
  }
  @include mq(pc-lg) {
    height: rem(770);
    transform: translateY(-48%) skewY(349deg);
  }
}

.p-contact__info {
  margin-top: 55px;
  padding-bottom: 5.5%;
  @include mq(md) {
    margin-top: 10%;
  }
  @include mq(pc-lg) {
    margin-top: 9.5%;
    padding-bottom: 6.5%;
  }
}