@use 'global'as *;

.p-mv {
  position: relative;
}

.p-mv__inner {
  @include mq(md) {
    width: 100%;
    margin: 0 auto;
  }
}

.p-mv__title {
  font-size: rem(20);
  font-weight: $normal;
  font-family: $second-font-family;
  text-transform: uppercase;
  letter-spacing: 0.01em;
  line-height: 1.35;
  text-align: center;
  @include mq(md) {
    font-size: rem(40);
    letter-spacing: 0.01em;
    font-weight: $normal;
    font-family: $second-font-family;
    margin-top: rem(5);
    white-space: nowrap;
  }
}

.p-mv__title-wrapper {
  position: absolute;
  top: 100%;
  left: 50%;
  width: 100%;
  transform: translate(-50%, -27%);
  display: flex;
  flex-direction: column-reverse;
  justify-content: center;
  @include mq(md) {
    top: 50%;
    left: 77%;
    transform: translate(-50%, -50%);
    width: unset;
  }
}

.p-mv__title-wrapper img {
  max-width: rem(147);
  width: 100%;
  margin: 0 auto;
  height: rem(103);
  @include mq(md) {
    max-width: 200px;
    height: auto;
  }
  @include mq(pc-md) {
    max-width: 408px;
  }
}

.p-mv__img-wrapper {
  display: block;
  @include mq(md) {
    max-width: rem(1566);
    width: 90%;
  }
  @include mq(pc-md) {
    max-width: unset;
    width: 82%;
  }
}

.p-mv__img-wrapper img {
  height: auto;
  object-fit: cover;
}

