@use 'global'as *;

.p-nav-pc {
  height: inherit;
}

.p-nav-pc__lists {
  display: flex;
  justify-content: flex-end;
  width: 100%;
  height: inherit;
}

.p-nav-pc__list {
  height: inherit;
  font-size: rem(24);
  font-weight: $normal;
  text-align: center;
  text-transform: uppercase;
  line-height: 1.5;
  font-family: $second-font-family;
  @include mq(md) {
    position: relative;
    display: flex;
    align-items: center;
    font-size: 12px;
  }
  // @include mq(md) {
  //   display: flex;
  //   align-items: flex-end;
  //   font-size: 16px;
  //   padding-bottom: 14px;
  // }
  @include mq(tab) {
    font-size: 14px;
  }
  @include mq(xl) {
    font-size: 20px;
  }
  @include mq(pc-md) {
    font-size: 24px;
  }
  @include mq(pc-lg) {
    position: relative;
    font-size: 24px;
    align-items: center;

  }
}

.p-nav-pc__list:not(:last-child) {
  margin-left: rem(55);
  @include mq(md) {
    padding: 0 17px;
    margin-right: 1%;
    margin-left: 0;
    // margin-left: 2%;
  }
  @include mq(tab) {
    margin-right: 1.5%;
    padding: 0 21px;
  }
  @include mq(xl) {
    padding: 0 25px;
  }
  @include mq(pc-md) {
    margin-right: 16px;
  }
  @include mq(pc-lg) {
    margin-right: 11px;
    padding: 0 22px;
  }
}



.p-nav-pc__list span {
  display: block;
  font-size: rem(16);
  letter-spacing: 0.01em;
  color: $colorSkyBlue;
  font-weight: $regular;
  font-family: $base-font-family;
  @include mq(md) {
    position: absolute;
    width: 100%;
    left: 50%;
    transform: translateX(-50%);
    font-size: 10px;
  }
  @include mq(tab) {
    font-size: 12px;
  }
  @include mq(xl) {
    font-size: 14px;
  }
  @include mq(pc-lg) {
    position: absolute;
    width: 100%;
    left: 50%;
    transform: translateX(-50%);
    font-size: 16px;
  }
}

.p-nav-pc__list--contact {
  margin-left: 35px;
  background: #676767;
  @include mq(md) {
    margin-left: 4%;
    padding-bottom: 0;
  }
  @include mq(xl) {
    padding-bottom: 0;
  }
  @include mq(pc-md) {
    margin-left: 23px;
  }
}

.p-nav-pc__list--contact a {
  @include mq(md) {
    display: flex;
    align-items: center;
    height: inherit;
    padding-right: 10px;
    padding-left: 10px;
    font-size: 12px;
    // font-size: rem(18);
    font-weight: $bold;
    font-family: $second-font-family;
    letter-spacing: 0.05em;
    color: $colorGreen;
  }
  @include mq(tab) {
    font-size: 16px;
  }
  @include mq(lg) {
    padding-right: 28px;
    padding-left: 28px;
    font-size: rem(24);
  }
  @include mq(xl) {
    font-size: 20px;
  }
  @include mq(pc-md) {
    padding: 0 32px 0 30px;
    font-size: 24px;
  }

}

.p-nav-pc__list--contact a::after {
  content: "";
  background: url(../images/icon-mail-green.svg) no-repeat center;
  background-size: contain;
  max-width: rem(26);
  width: 100%;
  height: rem(26);
  margin-left: 9.4px;
  flex: 0 0 auto;
  @include mq(md) {
    padding-right: 19px;
  }
  @include mq(pc-md) {
    margin-left: 7.4px;
    width: 25px;
    padding-right: 0;
  }
}