@use 'global'as *;

.p-accordion__item {
  padding: 13.3px 0 16.3px 0;
  padding: rem(13.3) 0 rem(16.3) 0;
  // padding: 18.3px 0 18.3px 0;
  border-top: solid 1px #707070;
  @include mq(md) {
    padding: 5.1% 3.3%;
  }
}

.p-accordion__item:last-child {
  border-bottom: solid 1px #707070;
}

.p-accordion__title {
  position: relative;
  display: flex;
  align-items: baseline;
  font-size: rem(14);
  font-weight: 600;
  line-height: 1.7;
  font-family: $third-font-family;
  transition: transform .5s;
  column-gap: rem(17);
  @include mq(md) {
    font-size: rem(40);
    font-weight: 600;
    letter-spacing: 0.02em;
  }
}

.p-accordion__title::before {
  content: "q";
  flex: 0 0 auto;
  // margin-right: rem(17);
  font-size: rem(20);
  text-transform: uppercase;
  font-family: $second-font-family;
  font-weight: 500;
  color: $colorSkyBlue;
  @include mq(md) {
    width: 7%;
    margin-right: rem(30);
    font-size: rem(50);
    font-weight: 500;
  }
  @include mq(pc-lg) {
    width: 5%;
  }
}

.p-accordion__title::after {
  content: "";
  position: relative;
  display: block;
  background: url(../images/icon-plus.svg) no-repeat;
  background-size: contain;
  width: rem(12);
  height: rem(12);
  margin-left: auto;
  // margin-left: rem(20);
  flex: 0 0 auto;
  right: rem(2);
  @include mq(md) {
    top: rem(3);
    width: rem(44);
    height: rem(44);
    margin-left: auto;
  }
  @include mq(tab) {
    position: absolute;
    top: 50%;
    right: 0;
    transform: translateY(-50%);
  }
}

.p-accordion__title.close::after {
    content: "";
    display: block;
    width: rem(12);
    height: rem(2);
    background-color: $colorSkyBlue;
    border-radius: 5px;
    position: relative;
    top: rem(-5);
    right: rem(2);
    @include mq(md) {
      position: absolute;
      top: 50%;
      right: 0;
      transform: translateY(-50%);
      max-width: rem(44);
      width: 100%;
      height: 4px;
    }
  }

.p-accordion__text {
  position: relative;
  display: none;
  width: 89%;
  margin-top: rem(20);
  margin-left: rem(35);
  margin-bottom: rem(3);
  padding: 0 rem(26);
  font-size: rem(14);
  font-weight: $regular;
  line-height: 1.7142;
  letter-spacing: .01em;
  @include mq(md) {
    width: 85%;
    margin-top: rem(33);
    margin-bottom: rem(19);
    padding-left: 10%;
    font-size: rem(30);
    font-weight: 300;
    line-height: 1.5;
  }
}

.p-accordion__text::before {
  content: "a";
  position: absolute;
  display: block;
  top: 0;
  left: 0;
  line-height: 1;
  font-size: rem(20);
  color: $colorSkyBlue;
  text-transform: uppercase;
  font-family: $second-font-family;
  @include mq(md) {
    font-size: clamp(20px, 4vw, 50px);
    font-size: rem(50);
    font-weight: 500;
    left: 5%;
  }
}

.p-accordion__question {
  display: flex;
}

.p-accordion__question.close .p-accordion__icon::after {
  display: none;
}

.p-accordion__link {
  color: #00CED1;
  text-decoration: underline;
}