@use 'global'as *;

.c-buger-button {
  /* ボタンの大きさ  */
  position: relative;
  width: rem(82);
  height: rem(84);
  cursor: pointer;
  /* 最前面に */
  z-index: 10;
  background: #fff;
  @include mq(md) {
    width: rem(80);
    height: rem(80);
  }
}

// .c-buger-button.open {
//   position: fixed;
//   top: 0;
//   right: 0;
// }

.c-buger-button__line {
	display: block;
	position: relative;  /* バーガー線の位置基準として設定 */
	width: rem(28);  /* 線の長さと高さ */
	height: 3px;
  margin: 0 auto;
  border-radius: 5px;
	background-color: $colorSkyBlue;  /* バーガー線の色 */
	transition: all .3s;
  @include mq(md) {
    width: rem(40);
    height: rem(4);
  }
}

.c-buger-button__line::before,
.c-buger-button__line::after {
  content: "";
  position: absolute;
  width: rem(28);
  height: 100%;
	background-color: $colorSkyBlue;  /* バーガー線の色 */
  left: 0;
  border-radius: 5px;
  transition: all .3s;
  @include mq(md) {
    width: rem(40);
  }
}
.c-buger-button__line::before {
  /* 上の線の位置 */
  transform: translateY(rem(-10));
}
.c-buger-button__line::after {
  /* 下の線の位置 */
  transform: translateY(rem(10));
}

/***** メニューオープン時 *****/
.c-buger-button__line.open {
	background-color: transparent;  	/* 真ん中の線を透明に */
}

.c-buger-button__line.open::before ,
.c-buger-button__line.open::after {
	content: "";
	background-color: $colorSkyBlue;  /* 上下の線の色を変える */
}

.c-buger-button__line.open::before {
	transform: rotate(45deg);  /* 上の線を傾ける */
}

.c-buger-button__line.open::after {
	transform: rotate(-45deg);  /* 下の線を傾ける */
}