@use 'global'as *;

.p-info__inner {
  text-align: right;
  padding-bottom: 42px;
  @include mq(md) {
    max-width: 1503px;
    width: 100%;
    margin: 0 auto;
    display: flex;
    flex-direction: row-reverse;
    justify-content: flex-start;
    align-items: center;
  }
}

.p-info__top {
  @include mq(md) {
    width: 60.3%;
    flex: 1 1 auto;
  }
}

.p-info__logo {
  display: inline-block;
  max-width: rem(287);
  width: 100%;
  padding-right: 9px;
  @include mq(md) {
    max-width: rem(809);
    padding-right: 0;
    margin-right: 4%;
  }
  @include mq(tab) {
    margin-right: 3%;
  }
  @include mq(xl) {
    margin-right: 2%;
  }
  @include mq(pc-lg) {
    margin-right: 1%;
  }
}

.p-info__address {
  margin-top: 13px;
  padding: 0 25px;
  font-size: rem(19);
  font-weight: $regular;
  line-height: 1.6315;
  text-align: right;
  color: #707070;
  @include mq(md) {
    font-size: 14px;
    margin-top: 60px;
    padding-right: 36px;
  }
  @include mq(tab) {
    font-size: 16px;
  }
  @include mq(pc-md) {
    font-size: rem(33);
  }
}

.p-info__text-weight {
  font-weight: $normal;
}

.p-info__text--color {
  font-weight: 600;
  color: $colorSkyBlue;
}

.p-info__list:not(:first-of-type) {
  margin-top: 22px;
  @include mq(md) {
    margin-top: 50px;
  }
  @include mq(pc-lg) {
    margin-top: 53px;
  }
}

.p-info__bottom {
  margin-top: 57.9px;
  @include mq(md) {
    width: 50%;
    margin-top: 0;
    flex: 1 1 auto;
  }
}

.p-info__logo-large {
  display: block;
  max-width: rem(147);
  width: 100%;
  margin: 0 auto;
  @include mq(md) {
    max-width: rem(380);
    display: inline-block;
    margin-right: 13%;
    margin-top: 19%;
  }
}

.p-info__logo-large img {
  height: auto;
}

