@use 'global'as *;

.p-values__lists {
  @include mq(md) {
    display: grid;
    grid-template-columns: 1fr 1fr;
    column-gap: 7%;
    row-gap: 18%;
    // row-gap: 22%;
  }
  @include mq(xl) {
    row-gap: 19%;
  }
  @include mq(pc-lg) {
    row-gap: 23.5%;
  }
}

.p-values__inner {
  max-width: rem(500);
  width: 100%;
  margin: 0 auto;
  padding: 15% 14%;
  @include mq(md) {
    max-width: rem(1920);
    padding: 13.5% 14%;
    padding: 9% clamp(25px, 6vw, 126px);
    // padding: 9% clamp(25px, 6.6vw, 126px);
  }
  @include mq(xl) {
    padding-top: 7%;
  }
  @include mq(pc-lg) {
    padding-top: 9%;
    padding-left: 108px;
    padding-right: 87px;
  }
}


.p-values__list {
  position: relative;
  display: flex;
  align-items: center;
  min-height: rem(65);
  padding: 7.5px 0;
  font-size: rem(16);
  line-height: 1.5625;
  font-weight: $regular;
  @include mq(md) {
    min-height: unset;
    width: 100%;
    font-size: 16px;
  }
  @include mq(xl) {
    font-size: 24px;
  }
  @include mq(pc-lg) {
    font-size: rem(45);
  }
}

.p-values__list:not(:first-of-type) {
  margin-top: 32px;
  @include mq(md) {
    margin-top: 0;
  }
}

.p-values__number {
  position: relative;
  max-width: rem(58);
  width: 100%;
  font-size: rem(20);
  font-weight: $normal;
  font-family: $second-font-family;
  top: 0;
  transform: translateY(-60%);
  @include mq(md) {
    margin-right: rem(80);
    font-size: 20px;
    letter-spacing: .01em;
  }
  @include mq(tab) {
    font-size: 26px;
  }
  @include mq(xl) {
    font-size: rem(40);
  }
  @include mq(pc-md) {
    margin-right: 16%;
    margin-right: clamp(40px, 7vw, 131px);
    font-size: rem(70);
  }
}

.p-values__number::before {
  content: "";
  position: absolute;
  display: block;
  left: 50%;
  bottom: -15%;
  width: rem(82);
  border-bottom: solid 2px $colorSkyBlue;
  transform: translate(-50%, 50%) rotate(132deg);
  @include mq(md) {
    border-bottom: solid 2px $colorSkyBlue;
    left: 50%;
    transform: translate(-24%, 50%) rotate(132deg);
    bottom: -4%;
    width: rem(160);
  }
  @include mq(pc-md) {
    border-bottom: solid 3px $colorSkyBlue;
    transform: translate(-15%, 50%) rotate(132deg);
    width: rem(220);
  }
  @include mq(pc-lg) {
    border-bottom: solid 5px $colorSkyBlue;
    transform: translate(-24%, 50%) rotate(132deg);
    width: 273px;
  }
}

.p-values__text--bule {
  color: $colorSkyBlue;
  font-weight: 600;
}