@use 'global'as *;

.p-card {
  background: $colorWhite;
  border-radius: 20px;
  // よければ採用
  box-shadow: 0px 3px 6px rgb(0 0 0 / 16%);
  @include mq(md) {
    width: 30%;
    background: #ffffff;
    border-radius: 30px;
  }
  @include mq(tab) {
    width: 29.86%;
    background: #ffffff;
    border-radius: 30px;
  }
}

.p-card:not(:first-child) {
  @include mq(md) {
    margin-left: 15px;
  }
  @include mq(lg) {
    margin-left: 3.21%;
  }
}

.p-card__title {
  margin-top: rem(12);
  font-size: rem(17);
  font-weight: $normal;
  letter-spacing: .01em;
  line-height: 1.5294;
  text-align: center;
  color: $colorSkyBlue;
  @include mq(md) {
    margin-top: rem(30);
    font-size: 16px;
    font-size: rem(30);
    line-height: 1.5;
    overflow-wrap: break-word;
	  word-break: keep-all;
  }
  @include mq(xl) {
    margin-top: rem(23);
    font-size: 20px;
    line-height: 1.5;
    overflow-wrap: break-word;
	  word-break: keep-all;
  }
  @include mq(pc-lg) {
    margin-top: rem(23);
    font-size: rem(30);
    line-height: 1.5;
    overflow-wrap: break-word;
	  word-break: keep-all;
  }
}

.p-card__title span {
  @include mq(md) {
    display: block;
  }
  @include mq(lg) {
    display: block;
  }
}

.p-card__text {
  font-size: rem(14);
  font-weight: $regular;
  line-height: 1.5714;
  @include mq(md) {
    font-size: rem(20);
    line-height: 1.5;
    letter-spacing: .01em;
  }
}

.p-card__text:first-of-type {
  margin-top: rem(20);
  @include mq(md) {
    margin-top: rem(33);
  }
}

.p-card__text:not(:first-of-type) {
  margin-top: rem(20);
  @include mq(md) {
    margin-top: rem(29);
  }
  @include mq(pc-lg) {
    margin-top: rem(35);
  }
}



// p-card--item-1
//=========================
.p-card--item-1.p-card {
  padding: 19px 27px 18px;
  padding: rem(19) rem(27) rem(18);
  @include mq(md) {
    padding: rem(38) 15px;
  }
  @include mq(xl) {
    padding: rem(38) 25px 3%;
  }
  @include mq(pc-lg) {
    padding: rem(38) 67px 5%;
  }
}

.p-card--item-1 img {
  max-width: rem(81);
  width: 100%;
  margin: 0 auto;
  @include mq(md) {
    max-width: rem(135.4);
    height: auto;
  }
  @include mq(pc-lg) {
    max-width: rem(142);
    height: auto;
  }
}
// p-card--item-1 end
//=========================


// p-card--item-2
//=========================
.p-card--item-2.p-card {
  padding: rem(31) rem(17) rem(23.2);
  @include mq(md) {
    padding: rem(54) 5px;
    // padding: rem(54) rem(28) rem(20);
  }
  @include mq(pc-lg) {
    padding-bottom: 30px;
  }
}

.p-card--item-2 img {
  max-width: rem(165);
  width: 100%;
  margin: 0 auto;
  @include mq(md) {
    max-width: rem(288);
    height: auto;
  }
}

.p-card--item-2 .p-card__title {
  margin-top: rem(27);
  @include mq(md) {
    margin-top: rem(54);
  }
}

.p-card--item-2 .p-card__text {
  padding: 0 8px;
  @include mq(md) {
    padding: 0 10px;
  }
  @include mq(lg) {
    padding: 0 25px;
  }
  @include mq(pc-lg) {
    padding: 0 63px;
  }
}
// p-card--item-2 end
//=========================


// p-card--item-3
//=========================
.p-card--item-3.p-card {
  padding: 26px 25px 16.5px;
  padding: rem(26) rem(25) rem(16.5);
  @include mq(md) {
    padding: 2.48% 15px;
  }
  @include mq(lg) {
    padding: 2.48% 25px;
  }
}

.p-card--item-3 img {
  max-width: rem(70.5);
  margin: 0 auto;
  @include mq(md) {
    max-width: rem(116.5);
    height: auto;

  }
}

.p-card--item-3 .p-card__title {
  margin-top: rem(18);
  @include mq(md) {
    margin-top: rem(37.5);
    margin: rem(37.5) rem(24);
  }
}

.p-card--item-3 .p-card__text {
  @include mq(pc-lg) {
    padding: 0 38px;
  }
}
// p-card--item-3 end
//=========================




