@use '../global' as *;

html {
  text-size-adjust: 100%;
  -webkit-tap-highlight-color: transparent; //タップの強調色解除
  /* フォントサイズの正確に */
  -webkit-text-size-adjust: 100%;
  /* 文字をシャープに */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  //~375px
  @media (max-width: 375px) {
    font-size: vw(375, 16);
  }

  //375px~787px
  font-size: 16px;

  @include mq("md") {
    font-size: vw(strip-unit($l-inner), 16);
  }

  //inner~max-screen
  @media (min-width: $l-inner) {
    font-size: 16px;
  }
}

// フォントファミリー, フォントカラー
body {
  font-family: $base-font-family;  
  color: $colorbase;
}

// スクリーンリーダー非表示 テキストを視覚的には隠したいけどスクリーンリーダーには伝えたい、デザイン上にタイトルはないけどマークアップで見出しを入れて構造化したい要件などを実現するために、要素を隠すような CSS を書きます。
.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0 0 0 0);
  clip-path: inset(50%);
  border: 0;
}

/* ホバー */
a {
  text-decoration: none;
  color: inherit;
  transition: opacity 0.3s;
  @include mq("md") {
    &:hover {
      opacity: 0.7;
    }
  }
}