.c-section-title {
  position: relative;
  display: flex;
  align-items: center;
  font-size: rem(35);
  line-height: 1.3142;
  letter-spacing: .01em;
  color: $colorSkyBlue;
  text-transform: uppercase;
  font-family: $second-font-family;
  @include mq(md) {
    font-size: rem(60);
    line-height: 1;
  }
}

.c-section-title::before {
  content: "";
  // position: absolute;
  // top: 50%;
  // transform: translateY(-50%);
  // left: 0;
  display: block;
  width: rem(20);
  height: rem(2);
  background-color: $colorSkyBlue;
  @include mq(md) {
    max-width: 94px;
    width: 100%;
    margin-right: 12px;
    // margin-right: rem(5);
  }
}

.c-section-title-sub {
  margin-left: rem(20);
  font-size: rem(12);
  font-weight: $regular;
  @include mq(md) {
    margin-top: 6px;
    margin-left: 106px;
    font-size: rem(30);
    line-height: 1.2;
    letter-spacing: 0.01em;
  }
}

.c-section-title-sub span {
  display: none;
  @include mq(md) {
    display: inline-block;
  }
}