@use 'global'as *;

.p-news {
  @include mq(md) {
    position: relative;
    z-index: 2;
  }
}

.p-news__inner {
  position: relative;
  width: 92.3%;
  padding: rem(15) rem(20) rem(9);
  border-radius: 0 0 0 5px;
  @include mq(md) {
    display: flex;
    transform: translateY(-37%);
    width: 70%;
    padding: 5px 20px 14px;
  }
  @include mq(lg) {
    width: 60%;
  }
  @include mq(xl) {
    position: relative;
    transform: translateY(-38%);
    max-width: rem(1051);
    width: 52.5%;
    display: flex;
  }
}

.p-news__inner::before {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  display: block;
  background: $colorbase;
  opacity: .8;
  border-radius: 0 20px 0 0;
  z-index: -1;
  @include mq(md) {
    border-radius: 0 30px 0 0;
  }
}



.p-news__title {
  font-size: rem(22);
  font-style: italic;
  font-family: $second-font-family;
  line-height: 1.3636;
  color: $colorWhite;
  text-transform: uppercase;
  @include mq(md) {
    font-size: 16px;
  }
  @include mq(xl) {
    font-size: 20px;
  }
  @include mq(pc-lg) {
    font-size: 45px;
  }
}

.p-news__lists {
  margin-top: rem(11);
  @include mq(md) {
    margin-top: 0;
    margin-left: rem(30);
  }
}

.p-news__list a {
  @include mq(md) {
    display: flex;
  }
}

.p-news__list a:hover .p-news__post-title {
  text-decoration: underline #fff;

}

.p-news__list time {
  font-size: rem(12);
  line-height: 1.6666;
  font-weight: $normal;
  color: $colorSkyBlue;
  @include mq(md) {
    font-size: 14px;
    letter-spacing: 0.01em;
    
  }
  @include mq(xl) {
    font-size: 16px;
  }
  @include mq(pc-lg) {
    font-size: 25px;
    line-height: 1.8;
  }
}

.p-news__post-title {
  font-size: rem(15);
  font-weight: $normal;
  line-height: 1.7333;
  color: $colorWhite;
  @include mq(md) {
    margin-left: rem(30);
    font-size: 14px;
    letter-spacing: 0.01em;
    text-decoration: underline transparent;
    transition: all .3s;
  }
  @include mq(xl) {
    font-size: 16px;
  }
  @include mq(pc-lg) {
    margin-left: rem(30);
    font-size: rem(25);
    letter-spacing: 0.01em;
    line-height: 1.8;
  }
}
