@use 'global'as *;

.p-header {
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  width: 100%;
  height: 84px;
  background: #fff;
  z-index: 10;
  @include mq(md) {
    height: 60px;
  }
  @include mq(tab) {
    height: 84px;
  }
  @include mq(xl) {
    height: 101px;
  }
}

.p-header__inner {
  width: 100%;
  height: inherit;
  padding-left: rem(21);
  @include mq(md) {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    padding-left: rem(40);
  }
}

.p-header__logo {
  display: flex;
  align-items: center;
  max-width: rem(190);
  width: 100%;
  height: inherit;
  @include mq(md) {
    max-width: 150px;
  }
  // @include mq(md) {
  //   max-width: 190px;
  // }
  @include mq(tab) {
    max-width: 200px;
  }
  @include mq(xl) {
    max-width: 300px;
  }
  @include mq(pc-lg) {
    max-width: 396px;
  }
}

.p-header__logo a {
  @include mq(md) {
    padding-top: rem(15);
  }
}

.p-header__logo img {
  height: auto;
}


.p-header__nav {
  display: none;
  @include mq(md) {
    display: block;
    width: 100%;
    height: inherit;
  }
  // @include mq(lg) {
  //   display: block;
  //   width: 100%;
  //   height: inherit;
  // }
}

.p-header__buger-button {
  position: absolute;
  top: 50%;
  right: 0;
  transform: translateY(-50%);
  @include mq(md) {
    display: none;
  }
  // @include mq(lg) {
  //   display: none;
  // }
}