@use 'global'as *;

.p-cards {
  @include mq(md) {
    display: flex;
    justify-content: center;
  }
}

.p-cards__items {
  position: relative;
}

.p-cards__items::before {
  content: "";
  position: absolute;
  background: $colorSkyBlue;
  top: 50%;
  left: 0;
  width: 100vw;
  // height: rem(322);
  height: 320px;
  margin: 0 calc(50% - 50vw);
  transform: translateY(-50%) skewY(337deg);
  transform-origin: top center;
  z-index: -1;
  @include mq(md) {
    display: none;
  }
}
