@use 'global'as *;

.p-footer {
  background: #676767;
}

.p-footer__inner {
  padding: 20% 5%;
  text-align: center;
  @include mq(md) {
    padding: 9.5% 5%;
  }
}

.p-footer__logo {
  max-width: rem(274);
  width: 100%;
  margin: 0 auto;
  @include mq(md) {
    max-width: rem(767);
  }
}

.p-footer__button {
  display: inline-block;
  max-width: rem(302);
  width: 100%;
  margin-top: 5px;
  padding: 11px 0;
  border-radius: 30px;
  font-size: rem(20);
  font-weight: $normal;
  background: #ffffff;
  @include mq(md) {
    max-width: rem(761);
    font-size: clamp(20px, 2.5vw, 40px);
    font-size: rem(40);
    border-radius: 43px;
    margin-top: 0;
    padding: rem(20) 0;
  }
}