@use 'global'as *;

.p-feature__inner {
  max-width: rem(400);
  width: 100%;
  margin: 12% auto 23%;
  padding:0 rem(35);
  @include mq(md) {
    position: relative;
    max-width: 1775px;
    width: 100%;
    margin: 6.41% auto 0;
    padding: 0;
    margin-top: 6.41%;
  }
  @include mq(xl) {
    margin-bottom: 123px;
    margin-top: 6.5%;
  }
}

.p-feature__inner::before {
  @include mq(md) {
    content: "";
    position: absolute;
    background: $colorSkyBlue;
    top: 50%;
    left: 0;
    width: 100vw;
    height: 340px;
    margin: 0 calc(50% - 50vw);
    transform: translateY(-50%) skewY(349deg);
    transform-origin: top center;
    z-index: -1;
  }
  @include mq(xl) {
    height: 750px;
  }
}