@use 'global'as *;

.p-about__inner {
  position: relative;
  display: flex;
  flex-direction: column-reverse;
  margin-top: 20px;
  @include mq(md) {
    flex-direction: row;
    margin-top: 50px;
    padding-left: 30px;
    align-items: center;
  }
  @include mq(xl) {
    margin-top: 56px;
    padding-left: 5.5%;
  }
}

.p-about__inner--reverse {
  @include mq(md) {
    flex-direction: row-reverse;
    padding-left: 0;
    padding-right: 30px;
  }
  @include mq(xl) {
    padding-left: 0;
    padding-right: 105px;
  }
}

.p-about__content {
  @include mq(md) {
    position: absolute;
    width: 65%;
  }
  @include mq(lg) {
    width: 70%;
  }
  @include mq(tab) {
    width: 65%;
  }
}

.p-about__text-inner--reverse {
  @include mq(md) {
    text-align: right;
  }
}

.p-about__inner + .p-about__inner {
  margin-top: 32px;
  @include mq(md) {
    margin-top: 13.1%;
  }
  @include mq(tab) {
    margin-top: 13.1%;
  }
  @include mq(pc-lg) {
    margin-top: 3.9%;
  }
}

.p-about__title {
  position: absolute;
  max-width: 400px;
  width: 92.5%;
  padding: rem(8) rem(20) rem(3);
  font-size: rem(17);
  font-weight: $normal;
  font-size: rem(17);
  font-weight: $normal;
  line-height: 1.5882;
  letter-spacing: 0.01em;
  color: $colorWhite;
  z-index: 2;
  background: rgba(0, 206, 209, 0.8);
  border-radius: 0 20px 0 0;
  transform: translateY(-50%);
  @include mq(md) {
    position: unset;
    transform: unset;
    max-width: 700px;
    width: 100%;
    padding: 20px 10px;
    font-size: 18px;
    line-height: 1.375;
  }
  @include mq(lg) {
    max-width: 800px;
    width: 90%;
    font-size: 20px;
  }
  @include mq(tab) {
    max-width: 800px;
    font-size: 20px;
  }
  @include mq(xl) {
    max-width: 1068px;
    // padding: 20px 24px;
    padding: 27px 24px;
    font-size: 22px;
    letter-spacing: 0.04em;
  }
  @include mq(pc-lg) {
    margin-top: 12px;
    font-size: rem(40);
  }
}

.p-about__title--reverse {
  right: 0;
  border-radius: 20px 0 0 0;
  text-align: right;
  @include mq(md) {
    margin-left: auto;
  }
}

.p-about__text-inner {
  margin-top: rem(70);
  padding: 0 29px 0 20px;
  @include mq(md) {
    margin-top: 4.83%;
    padding: 0;
  }
  @include mq(xl) {
    margin-top: 3.83%;
  }
}

.p-about__text {
  font-size: rem(14);
  font-weight: $regular;
  line-height: 1.8571;
  text-align: justify;
  @include mq(md) {
    // font-size: rem(25);
    font-size: 14px;
  }
  @include mq(tab) {
    // font-size: rem(25);
    font-size: 16px;
  }
  @include mq(pc-md) {
    font-size: rem(25);
  }
  @include mq(pc-lg) {
    font-size: 25px;
  }
}

.p-about__text:first-of-type {
  margin-top: 2.44%;
  @include mq(md) {
    margin-top: 0;
  }
}

.p-about__text:not(:first-of-type) {
  margin-top: 8%;
  @include mq(md) {
    margin-top: 1.93%;
  }
  @include mq(xl) {
    margin-top: 3.5%;
  }
}

.p-about__img {
  @include mq(md) {
    margin-left: auto;
    width: 60%;
  }
  @include mq(lg) {
    width: 50%;
  }
  @include mq(xl) {
    width: 56%;
  }
}

.p-about__img--reverse {
  @include mq(md) {
    margin-left: unset;
    margin-right: auto;
  }
}

.p-about__img-wrapper {
  display: block;
  width: 94.7%;
  margin-left: auto;
}

.p-about__img-wrapper--reverse {
  margin-left: unset;
}

.p-about__img-wrapper img {
  height: auto;
}

