@use 'global'as *;

.p-products__lavina {
  margin-top: 39px;
  @include mq(md) {
    margin-top: 7%;
  }
  @include mq(pc-lg) {
    margin-top: 7.5%;
  }
}

.p-products__head {
  position: relative;
  text-align: center;
}

.p-products__text-wrapper {
  position: absolute;
  left: 0;
  bottom: 0;
  transform: translateY(89%);
  @include mq(md) {
    width: 100%;
    transform: translateY(78%);    
  }
  @include mq(xl) {
    transform: translateY(78%);  
  }
  @include mq(pc-md) {
    transform: translateY(68%);    
  }
}

.p-products__img-wrapper {
  position: relative;
  display: block;
  width: 94.3%;
}

.p-products__img-wrapper::before {
  content: "";
  display: block;
  padding-top: 56.25%;

}

.p-products__img-wrapper img {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  clip-path: polygon(100% 0, 100% 98%, 53% 81%, 0 100%, 0 0);
}

.p-products__text-wrapper {
  padding: 0 20px;
}

.p-products__text {
  margin-top: 31px;
  padding: 0 5.334%;
  font-size: rem(14);
  font-weight: $regular;
  text-align: left;
  line-height: 1.8571;
  letter-spacing: .01em;
  @include mq(md) {
    max-width: 1645px;
    width: 86%;
    margin-top: 20px;
    margin-left: auto;
    font-size: clamp(14px, 2vw, 30px);
    line-height: 1.5;
  }
  @include mq(lg) {
    margin-top: 0;
  }
 
}

.p-products__text--left {
  @include mq(md) {
    margin-left: 0;
    margin-right: auto;
    padding: 0 5%;
  }
  
}

.p-products__logo {
  display: inline-block;
  @include mq(md) {
    max-width: 482px;
    width: 26%;
  }
}

.p-products__logo img {
  @include mq(md) {
    height: auto;
  }
}

.p-products__head-title {
  margin-top: rem(2);
  font-size: rem(15);
  font-weight: $normal;
  letter-spacing: .01em;
  line-height: 1.5333;
  color: #000000;
  @include mq(md) {
    margin-top: rem(27);
    font-size: clamp(15px, 1.6vw, 30px);
    line-height: 1.5;
  }
}

.p-products__head-text {
  margin-top: rem(14);
  font-size: rem(14);
  font-weight: $regular;
  color: #000000;
  line-height: 1.5714;
  text-align: left;
  @include mq(md) {
    font-size: clamp(14px, 2vw, 25px);
    max-width: 1073px;
    width: 80%;
    margin: 30px auto 0;
  }
}


.p-products__flex-container {
  @include mq(md) {
    display: flex;
    align-items: flex-end;
  }
}

.p-products__flex-container--reverse {
  @include mq(md) {
    flex-direction: row-reverse;
  }
}

.p-products__content + .p-products__content {
  margin-top: 44px;
  @include mq(md) {
    margin-top: 9.5%;
  }
}

.p-products__content-img-wrapper {
  display: block;
  @include mq(md) {
    width: 45%;
    margin-left: auto;
  }
  @include mq(pc-lg) {
    width: 53%;
    margin-left: 60px;
  }
}

.p-products__img-1 {
  width: 89%;
  @include mq(pc-lg) {
    width: 98%;
  }
}

.p-products__img-2 {
  width: 85%;
  margin-left: auto;
  @include mq(md) {
    margin-left: 0;
    margin-right: auto;
    width: 96%;
  }
}

.p-products__content-img-wrapper:nth-of-type(2) {
  width: 85%;
}

.p-products__content-img-wrapper img {
  height: 100%;
}

.p-products__content--lavina {
  margin-top: 193px;
  @include mq(lg) {
    margin-top: 23.5%;
  }
  @include mq(pc-lg) {
    margin-top: 21%;
  }
}

.p-products__sub-title-wrapper {
  background: $colorSkyBlue;
  color: $colorWhite;
  max-width: rem(300);
  width: 80%;
  padding: 9px 21px 13px 20px;
  margin-top: 6px;
  margin-left: auto;
  text-align: right;
  border-radius: 20px 0 0 20px;
  @include mq(md) {
    max-width: unset;
    width: 45%;
    padding-right: 6.2%;
    padding-top: rem(36);
    padding-bottom: rem(16);
    padding-left: rem(20);
  }
  @include mq(lg) {
    width: 43%;
    margin-bottom: 30px;
  }
}

.p-products__sub-title-wrapper--reverse {
  border-radius: 0 20px 20px 0;
  margin-right: auto;
  margin-left: 0;
  text-align: left;
  @include mq(md) {
    padding-left: 6.2%;
    padding-right: rem(20);
  }
  @include mq(pc-lg) {
    margin-right: 0;
  }
}

.p-products__sub-title {
  font-size: rem(20);
  letter-spacing: .01em;
  line-height: 1.35;
  text-transform: uppercase;
  font-family: $second-font-family;
  @include mq(md) {
    font-size: clamp(20px, 3vw, 60px);
  }
}

.p-products__sub-text {
  margin-top: rem(3);
  font-size: rem(15);
  font-weight: $normal;
  letter-spacing: .01em;
  line-height: 1.5333;
  @include mq(md) {
    margin-top: 21px;
    line-height: 1.75;
    font-size: clamp(15px, 2vw, 40px);
  }
}

.p-products__flex-container--reverse .p-products__content-img-wrapper {
  @include mq(md) {
    margin-left: 0;
    margin-right: auto;
  }
}


.p-products__content--toguya {
  margin-top: 193px;
  @include mq(md) {
    margin-top: 35%;
  }
  @include mq(xl) {
    margin-top: 30%;
  }
  @include mq(pc-lg) {
    margin-top: 20%;
  }

}

.p-products__toguya {
  margin-top: 44px;
  @include mq(md) {
    margin-top: 9.5%;
  }
}

.p-products__logo--toguya {
  @include mq(md) {
    max-width: 225px;
    width: 15%;
  }
}

.p-products__content--toguya .p-products__content-img-wrapper {
  @include mq(md) {
    margin-bottom: -2%;
    z-index: -1;
  }
}

.p-products__toguya .p-products__text-wrapper {
  transform: translateY(83%);
  @include mq(md) {
    transform: translateY(90%);
  }
  @include mq(pc-lg) {
    transform: translateY(67%);
  }
}

.p-products__img-3 {
  width: 76%;
  @include mq(lg) {
    width: 100%;
  }
}
.p-products__img-4 {
  width: 66%;
  margin-left: auto;
  @include mq(md) {
    margin: 0 auto;
    width: 68%;
  }
}


